<template>
  <div class="search">

    <div class="search-nav">
      <van-nav-bar left-arrow @click-left="goback" @click-right="clicksearch">
        <template #title>
          <input @change="getsearch" class="search-inp" type="text" v-model="searchactive" :placeholder="aaaa"/>
        </template>
        <template #right>
          <van-icon name="search" size="18" />
        </template>
      </van-nav-bar>
    </div>
        <router-view/>
  </div>
</template>

<script>
import "../assets/less/search.less";
export default {
  data() {
    return {
      hotdata:[],
      aaaa:'',
      searchactive:'',
      show:false
    };
  },
  created() {
    //热搜推荐
    // this.getHotRm()
    //默认关键词
    this.getDefault()
  },
  methods: {
     //返回
    goback() {
      this.$router.go(-1);
    },
    
    //搜索输入
    getsearch(){
      if (this.searchactive) {
        this.$router.replace({path:'/searchDetail',query:{wkey:this.searchactive}})  
      }else{
        this.$router.replace({path:'/searchHot'})  
      }
    },
    //点击搜索图标
    clicksearch(){
      if (this.searchactive) {
        this.$router.replace({path:'/searchDetail',query:{wkey:this.searchactive}})  
      }else{
         this.$router.replace({path:'/searchDetail',query:{wkey:this.aaaa}})  
      }
    },
    //默认搜索关键词
    getDefault() {
      this.axios({
        method: "GET",
        url:"/search/default",
      })
        .then((result) => {
          if (result.data.code == 200) {
            this.aaaa = result.data.data.realkeyword
          }
        })
        .catch((err) => {
          console.error(err)
        });
    },
  },
};
</script>